.aboutUs,
.contactUs,
.learn,
.support {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-6xl);
  font-family: var(--font-josefin-sans);
  color: var(--color-black);
  text-align: center;
  display: inline-block;
  width: 142px;
  flex-shrink: 0;
  z-index: 3;
}
.aboutUs,
.contactUs,
.support {
  z-index: 2;
}
.contactUs,
.support {
  z-index: 1;
}
.contactUs {
  z-index: 0;
}
.footer {
  position: absolute;
  top: 935px;
  left: 0;
  background-color: var(--color-gainsboro);
  width: 1440px;
  height: 88px;
  display: flex;
  flex-direction: row;
  padding: 33px 405px;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.adminLogin {
  position: relative;
  font-size: var(--font-size-15xl);
  font-family: var(--font-josefin-sans);
  color: var(--color-white);
  text-align: left;
}
.buttonadminlogin,
.buttonreviewerlogin {
  cursor: pointer;
  border: 0;
  padding: var(--padding-lg) var(--padding-76xl);
  background-color: var(--color-darkcyan);
  position: absolute;
  top: 779px;
  left: 277px;
  border-radius: var(--br-15xl);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.buttonreviewerlogin {
  left: 778px;
}
.grepELicensing {
  font-size: 35px;
  text-align: left;
  display: inline-block;
  width: 286.66px;
  height: 30px;
}
.appicon,
.grepELicensing,
.newUser {
  position: relative;
  flex-shrink: 0;
}
.appicon {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  border-radius: var(--br-15xl);
  width: 118px;
  height: 94px;
  background-image: url(/public/appicon@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.newUser {
  display: inline-block;
  width: 120px;
  height: 20px;
}
.buttonsignup {
  cursor: pointer;
  border: 0;
  padding: 0 7px;
  background-color: var(--color-darkcyan);
  border-radius: var(--br-15xl);
  width: 106px;
  height: 38px;
  flex-shrink: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.alreadyHaveAn,
.signIn {
  position: relative;
  flex-shrink: 0;
}
.alreadyHaveAn {
  display: inline-block;
  width: 289px;
  height: 20px;
}
.signIn {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  font-size: var(--font-size-4xl);
  font-family: var(--font-josefin-sans);
  color: var(--color-white);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 104px;
  height: 46px;
}
.buttonsignin,
.maincontainer {
  border-radius: var(--br-15xl);
  overflow: hidden;
  display: flex;
}
.buttonsignin {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-darkcyan);
  width: 104px;
  height: 46px;
  flex-shrink: 0;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.maincontainer {
  position: absolute;
  top: 193px;
  left: 479px;
  background-color: var(--color-gainsboro);
  flex-direction: column;
  padding: 81px 102px;
  align-items: center;
  justify-content: center;
  gap: 14px;
}
.landingPage {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 1024px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-4xl);
  color: var(--color-black);
  font-family: var(--font-josefin-sans);
}
